import { useEffect, useState } from "react";
import isEmpty from "lodash/isEmpty";
import find from "lodash/find";
import flatMap from "lodash/flatMap";
import orderBy from "lodash/orderBy";
import {
  Alert,
  Autocomplete,
  Backdrop,
  Box,
  Button,
  CircularProgress,
  FormControl,
  Snackbar,
  TextField,
} from "@mui/material";
import {
  candidateEnum,
  District,
  Division,
  ElectionResults,
  VoteUpdatePayload,
} from "./types";
import Grid from "@mui/material/Grid2";
import { fetchElectionData, updateVotes } from "./apis";

const Admin = () => {
  const [district, setDistrict] = useState<KeyValue | null>(null);
  const [division, setDivision] = useState<KeyValue | null>(null);

  const [districts, setDistricts] = useState<District[]>([]);
  const [divisions, setDivisions] = useState<Division[]>([]);

  const [total, setTotal] = useState("");
  const [akd, setAkd] = useState("");
  const [sp, setSp] = useState("");
  const [rw, setRw] = useState("");

  const [electionData, setElectionData] = useState<ElectionResults | null>(
    null
  );
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [open, setOpen] = useState<boolean>(false);

  const loadData = async () => {
    setLoading(true);
    try {
      const data = await fetchElectionData();
      setElectionData(data);
      buildDistrictList(data);
    } catch (error) {
      setError("Failed to fetch election data.");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadData();
  }, []);

  const buildDistrictList = (data: ElectionResults) => {
    const districts = flatMap(
      data.provinces,
      (province) => province.districts
    ).filter((x) => x.divisions.some((y) => !y.totalValidVotes));
    setDistricts(orderBy(districts, ["name"], ["asc"]));
  };

  const buildDivisionList = (districtName: string) => {
    const result = find(districts, { name: districtName });
    setDivisions(
      result ? result.divisions.filter((x) => !x.totalValidVotes) : []
    );
  };

  const handleChangeDistrict = (value: KeyValue | null) => {
    if (value) {
      setDistrict(value);
      buildDivisionList(value.value);
    }
  };
  const handleChangeDivision = (value: KeyValue | null) => {
    if (value) setDivision(value);
  };

  let isButtonDisabled: boolean = false;

  const getNumber = (txt: string): number => {
    return Number(txt);
  };

  const handleSubmit = () => {
    if (
      district?.value &&
      division?.value &&
      total.length > 0 &&
      akd.length > 0 &&
      sp.length > 0 &&
      rw.length > 0 &&
      Number(akd) + Number(sp) + Number(rw) <= Number(total)
    ) {
      setLoading(true);
      const payload: VoteUpdatePayload = {
        district: district.value,
        division: division.value,
        totalValidVotes: getNumber(total) ?? 0,
        akdVotes: getNumber(akd) ?? 0,
        spVotes: getNumber(sp) ?? 0,
        rwVotes: getNumber(rw) ?? 0,
      };

      updateVotes(payload)
        .then((response) => {
          console.log("API response:", response.success);
          setDistrict(null);
          setDivision(null);
          setTotal("");
          setAkd("");
          setSp("");
          setRw("");
          setOpen(true);
          loadData();
        })
        .catch((error) => {
          console.error("API call failed:", error);
        })
        .finally(() => {
          setLoading(false);
        });
    }
  };

  interface KeyValue {
    label: string;
    value: string;
  }

  return (
    <>
      <Backdrop
        sx={(theme) => ({ color: "#fff", zIndex: theme.zIndex.drawer + 1 })}
        open={loading}
      >
        <CircularProgress color="inherit" />
      </Backdrop>

      <Grid container width="100%" spacing={2} padding={4}>
        <Snackbar
          open={open}
          autoHideDuration={6000}
          onClose={() => setOpen(false)}
        >
          <Alert
            onClose={() => setOpen(false)}
            severity={error && error.length ? "error" : "success"}
            variant="filled"
            sx={{ width: "100%" }}
          >
            {error && error.length
              ? "Submitting votes failed!"
              : "Submitting votes successful!"}
          </Alert>
        </Snackbar>
        <Grid size={{ xs: 12, lg: 4 }}>
          <FormControl fullWidth>
            <Autocomplete
              disablePortal
              value={district}
              options={districts.map((d) => ({ label: d.name, value: d.name }))}
              onChange={(_, v) => handleChangeDistrict(v)}
              renderInput={(params) => (
                <TextField {...params} value={district} label="DISTRICT" />
              )}
            />
          </FormControl>
        </Grid>
        <Grid size={{ xs: 12, lg: 4 }}>
          <FormControl fullWidth disabled={isEmpty(district)}>
            <Autocomplete
              disablePortal
              value={division}
              disabled={isEmpty(district)}
              options={divisions.map((d) => ({ label: d.name, value: d.name }))}
              onChange={(_, v) => handleChangeDivision(v)}
              renderInput={(params) => (
                <TextField {...params} value={division} label="DIVISION" />
              )}
            />
          </FormControl>
        </Grid>
        <Grid size={{ xs: 12, lg: 4 }}>
          <FormControl fullWidth>
            <TextField
              id="outlined-basic"
              type="number"
              label="TOTAL"
              value={total}
              onChange={(e) => setTotal(e.target.value)}
              variant="outlined"
            />
          </FormControl>
        </Grid>
        <Grid size={{ xs: 12, lg: 4 }}>
          <FormControl fullWidth>
            <TextField
              id="outlined-basic"
              type="number"
              label={candidateEnum.get("akd")}
              onChange={(e) => setAkd(e.target.value)}
              value={akd}
              variant="outlined"
            />
          </FormControl>
        </Grid>
        <Grid size={{ xs: 12, lg: 4 }}>
          <FormControl fullWidth>
            <TextField
              id="outlined-basic"
              type="number"
              label={candidateEnum.get("sp")}
              onChange={(e) => setSp(e.target.value)}
              value={sp}
              variant="outlined"
            />
          </FormControl>
        </Grid>
        <Grid size={{ xs: 12, lg: 4 }}>
          <FormControl fullWidth>
            <TextField
              id="outlined-basic"
              type="number"
              label={candidateEnum.get("rw")}
              value={rw}
              onChange={(e) => setRw(e.target.value)}
              variant="outlined"
            />
          </FormControl>
        </Grid>
        <Grid size={{ xs: 12, lg: 4 }} sx={{ margin: "30px auto" }}>
          <FormControl fullWidth>
            <Button
              variant="contained"
              size="large"
              disabled={isButtonDisabled}
              onClick={handleSubmit}
            >
              Submit
            </Button>
          </FormControl>
        </Grid>
      </Grid>
    </>
  );
};

export default Admin;
